.guarantee__title {
  @media screen and (min-width: $desktop) {
    margin-top: 25px;
  }
}

.guarantee__item::before {
  content: '';
  display: block;
  width: 13px;
  height: 13px;
  margin-bottom: 24px;
  border-radius: 50%;
  background-color: $primary-color;
}

.guarantee__subtitle {
  @media screen and (max-width: ($tablet - 1px)) {
    width: 152px;
  }

  @media screen and (min-width: $tablet) {
    margin-bottom: 20px;
  }
}

.guarantee__text {
  &--first {
    margin-bottom: 24px;

    @media screen and (min-width: $tablet) {
      margin-bottom: 55px;
    }

    @media screen and (min-width: $desktop) {
      margin-bottom: 69px;
    }
  }

  @media screen and (max-width: ($tablet - 1px)) {
    &--last {
      margin-bottom: 32px;
    }
  }

  @media screen and (min-width: $desktop) {
    width: 400px;
  }
}

.guarantee__wrap-image {
  flex-shrink: 0;

  background-color: #1d1d1d;

  @media screen and (max-width: ($tablet - 1px)) {
    margin-left: auto;
    margin-right: auto;
  }
  @media screen and (min-width: $tablet) {
    margin-left: -60px;
    margin-right: 60px;
  }

  @media screen and (min-width: $desktop) {
    margin-left: -190px;
    margin-right: 150px;
  }
}

.guarantee__image {
  @media screen and (max-width: ($tablet - 1px)) {
    margin-left: auto;
    margin-right: auto;
  }
}
